/** END BLOCK PAGE SCROLL */

/** BEGIN ICONS **/
.cc_div .b-bn .b-tl::before,
.cc_div .act .b-bn .b-tl::before{
    border: solid #2d4156;
    border-color: var(--cc-btn-secondary-text);
    border-width: 0 2px 2px 0;
    padding: .2em;
    display: inline-block;
    position: absolute;
    content: '';
    margin-right: 15px;
    position: absolute;
    transform: translateY(-50%) rotate(45deg);
    left: 1.2em;
    top: 50%;
}

.cc_div .act .b-bn .b-tl::before{
    transform: translateY(-20%) rotate(225deg);
}

.cc_div .on-i::before{
    border: solid #ffffff;
    border-color: var(--cc-toggle-knob-icon-color);
    border-width: 0 2px 2px 0;
    padding: .13em;
    display: inline-block;
    padding-bottom: .55em;
    content: '';
    margin: 0 auto;
    transform: rotate(45deg);
    margin-top: .31em;
    margin-left: .1em;
}

#s-c-bn::before,
#s-c-bn::after{
    content: '';
    position: absolute;
    left: .82em;
    top: .58em;
    height: .6em;
    width: 1.5px;
    background: #444d53;
    background: var(--cc-btn-secondary-text);
    transform: rotate(45deg);
    border-radius: 1em;
    margin: 0 auto;
}

#s-c-bn::after{
    transform: rotate(-45deg);
}

.cc_div .off-i,
.cc_div .on-i{
    height: 100%;
    width: 50%;
    position: absolute;
    right: 0;
    display: block;
    text-align: center;
    transition: opacity .25s ease;
}

.cc_div .on-i{
    left: 0;
    opacity: 0;
}

.cc_div .off-i::before,
.cc_div .off-i::after{
    right: .84em;
    top: .4em;
    content: ' ';
    height: .85em;
    width: 0.09375em;
    display: block;
    background: #cdd6dc;
    background: var(--cc-toggle-knob-icon-color);
    margin: 0 auto;
    position: absolute;
    transform-origin: center;
}

.cc_div .off-i::before {
    transform: rotate(45deg);
}
.cc_div .off-i::after {
    transform: rotate(-45deg);
}

.cc_div .c-tgl:checked ~ .c-tg .on-i{
    opacity: 1;
}
.cc_div .c-tgl:checked ~ .c-tg .off-i{
    opacity: 0;
}
/** END ICONS **/


