$name: lg;  
$size: map-deep-get($breakpoints, "lg", "width"); 


@media only screen and (min-width: $size) {


/*
================================================================
VARIABLES
================================================================
*/

:root {
    --pageMargins: 30px;
  }
  

/*
================================================================
ARTIST
================================================================
*/

.artist-title{
  max-width: 700px;
}

.artist-index-grid{
  grid-template-columns: repeat(3, 1fr);
}

.bio-grid{
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 60px;
  display: grid;
}

.artist-bio-text{
  grid-column: 2 / span 3;
  grid-row: 1;
}

.artist-contact{
  grid-column: 1 / span 1;
  grid-row: 1;
}

.slideshow{
  z-index: 2;
  pointer-events: none;
}

.next-slide{
  transform: translateX(0);
  // background-color: transparent;
  cursor: url("../components/next.svg"), pointer;
  z-index: 1;
}

.prev-slide{
  transform: translateX(0);
  // background-color: transparent;
  cursor: url("../components/prev.svg"), pointer;
  z-index: 1;
}



} /* End Media Query */